.main__certificate {
  padding: 120px 0 110px;
  background-color: #3E416D;
}

.main__certificate-flex {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}

.main__certificate-flex-block-title {
  font-weight: 700;
  font-size: 40px;
  line-height: 48px;
  color: #FFFFFF;
}

.main__certificate-flex-block-text {
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  color: #FFFFFF;
  max-width: 577px;
  padding-top: 30px;
}

.main__certificate-flex-block-about {
  display: flex;
  justify-content: space-between;
  padding-top: 40px;
  flex-wrap: wrap;
}

.main__certificate-flex-block-about-get-title {
  font-weight: 600;
  font-size: 18px;
  line-height: 22px;
  color: #FFFFFF;
}

.main__certificate-flex-block-about-get-text {
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  color: #FFFFFF;
  padding-top: 20px;
}

.main__certificate-flex-block-about-get-link {
  margin-top: 20px;
  display: flex;
  flex-direction: column;
}

.main__certificate-flex-block-about-get-link-start {
  text-decoration: none;
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
  text-decoration-line: underline;
  color: #8482FF;
  padding-left: 25px;
  position: relative;
}

.main__certificate-flex-block-about-get-link-start::after {
  position: absolute;
  content: "";
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background-color: #8482FF;
  left: 0;
  top: 3px;
}

.main__certificate-flex-block-about-get-link-pro {
  text-decoration: none;
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
  text-decoration-line: underline;
  color: #FF9882;
  padding-left: 25px;
  margin-top: 10px;
  position: relative;
